import React from 'react';
import { Link, animateScroll as scroll } from 'react-scroll';
import arrowUp from '../../images/arrowUp.svg';

export default function ButtonTop() {
  function handleClick(evt) {
    evt.preventDefault();
    scroll.scrollToTop();
  }

  return (
    <div className="buttonTop-container" id="buttonBox">
      <Link to="#top" smooth duration={500} onClick={handleClick}>
        <img src={arrowUp} alt="Arrow Up" className="buttonTop-arrowUp" />
      </Link>
    </div>
  );
}
